import Header from '@/components/Header';
import React, { useState } from 'react';

const EventsPage = () => {
  const [statusFilter, setStatusFilter] = useState('all');
  const [isLoading, setIsLoading] = useState(false);

  // Sample events data
  const events = [
    {
      id: 1,
      title: "Campus Sustainability Workshop",
      date: "2025-11-25",
      duration: "3 hours",
      fee: "Free",
      status: "upcoming",
      tags: ["Workshop", "Education"],
      participants: 45,
      description: "Learn practical sustainability techniques for campus life"
    },
    {
      id: 2,
      title: "Green Tech Innovation Fair",
      date: "2025-12-02",
      duration: "1 day",
      fee: "$10",
      status: "upcoming",
      tags: ["Innovation", "Technology"],
      participants: 120,
      description: "Showcase of sustainable technology solutions"
    },
    {
      id: 3,
      title: "Zero Waste Challenge",
      date: "2025-11-20",
      duration: "Ongoing",
      fee: "Free",
      status: "present",
      tags: ["Challenge", "Community"],
      participants: 89,
      description: "Join our month-long zero waste initiative"
    }
  ];

  const filteredEvents = events.filter(event => {
    if (statusFilter === 'all') return true;
    return event.status === statusFilter;
  });

  return (
    <div className="min-h-screen relative bg-stone-50 font-sans overflow-x-hidden select-none">
      {/* Decorative SVG Background Elements */}
      <div className="z-[1] w-screen h-full absolute overflow-x-hidden pointer-events-none">
        <div className="max-md:left-[-530px] absolute -left-96 top-[500px]">
          <img src="https://proxy.extractcss.dev/https://sustainablewebdesign.org/wp-content/themes/swdorg/public/images/blue-triangle.svg" alt="" />
        </div>
        <div className="absolute max-md:-left-96 top-[-200px] -left-56">
          <img src="https://proxy.extractcss.dev/https://sustainablewebdesign.org/wp-content/themes/swdorg/public/images/orange-square.svg" alt="" />
        </div>
        <div className="absolute max-md:right-[-1220px] right-[-1180px] top-[300px]">
          <img src="https://proxy.extractcss.dev/https://sustainablewebdesign.org/wp-content/themes/swdorg/public/images/orange-square.svg" alt="" />
        </div>
        <div className="absolute max-md:right-[-720px] -right-96 -top-52">
          <img src="https://proxy.extractcss.dev/https://sustainablewebdesign.org/wp-content/themes/swdorg/public/images/yellow-triangle.svg" alt="" />
        </div>
        <div className="absolute -left-96 top-[1200px]">
          <img src="https://proxy.extractcss.dev/https://sustainablewebdesign.org/wp-content/themes/swdorg/public/images/yellow-triangle.svg" alt="" />
        </div>
        <div className="max-md:right-0 absolute right-12 top-[1000px]">
          <img src="https://proxy.extractcss.dev/https://sustainablewebdesign.org/wp-content/themes/swdorg/public/images/blue-blob.svg" alt="" />
        </div>
        <div className="absolute max-md:-left-80 -left-72 top-[800px]">
          <img src="https://proxy.extractcss.dev/https://sustainablewebdesign.org/wp-content/themes/swdorg/public/images/red-rectangle.svg" alt="" />
        </div>
      </div>

      {/* Main Content */}
      <div className="z-10 relative">
        <div className="max-w-screen-xl mx-auto px-5 py-12">
          {/* Header */}
          <div className="mb-16">
            <h1 className="my-8 max-md:text-5xl font-black text-7xl leading-tight text-green-700">
              Events
            </h1>
            <p className="my-5 max-w-3xl font-sans text-2xl leading-normal text-black">
              Discover and participate in our sustainability events
            </p>
          </div>

          {/* Content Grid */}
          <div className="flex gap-8 max-lg:flex-col">
            {/* Filters Sidebar */}
            <div className="max-lg:w-full w-96 shrink-0">
              <div className="bg-white p-8 rounded-3xl border-4 border-black shadow-lg">
                {/* Status Filter */}
                <div className="mb-8">
                  <h2 className="font-black text-2xl mb-4 text-black">Status</h2>
                  <div className="flex flex-wrap gap-3">
                    <button
                      onClick={() => setStatusFilter('all')}
                      className={`px-6 py-2.5 rounded-full font-bold text-lg border-4 border-black transition-colors ${
                        statusFilter === 'all'
                          ? 'bg-teal-400 text-black'
                          : 'bg-white text-black hover:bg-teal-100'
                      }`}
                    >
                      All
                    </button>
                    <button
                      onClick={() => setStatusFilter('upcoming')}
                      className={`px-6 py-2.5 rounded-full font-bold text-lg border-4 border-black transition-colors ${
                        statusFilter === 'upcoming'
                          ? 'bg-teal-400 text-black'
                          : 'bg-white text-black hover:bg-teal-100'
                      }`}
                    >
                      Upcoming
                    </button>
                    <button
                      onClick={() => setStatusFilter('present')}
                      className={`px-6 py-2.5 rounded-full font-bold text-lg border-4 border-black transition-colors ${
                        statusFilter === 'present'
                          ? 'bg-teal-400 text-black'
                          : 'bg-white text-black hover:bg-teal-100'
                      }`}
                    >
                      Present
                    </button>
                    <button
                      onClick={() => setStatusFilter('past')}
                      className={`px-6 py-2.5 rounded-full font-bold text-lg border-4 border-black transition-colors ${
                        statusFilter === 'past'
                          ? 'bg-teal-400 text-black'
                          : 'bg-white text-black hover:bg-teal-100'
                      }`}
                    >
                      Past
                    </button>
                  </div>
                </div>

                {/* Tags Section */}
                <div className="mb-8">
                  <h2 className="font-black text-2xl mb-4 text-black">Tags</h2>
                  <div className="flex flex-wrap gap-2">
                    <span className="px-4 py-2 bg-lime-300 rounded-full text-sm font-bold border-2 border-black">Workshop</span>
                    <span className="px-4 py-2 bg-lime-300 rounded-full text-sm font-bold border-2 border-black">Innovation</span>
                    <span className="px-4 py-2 bg-lime-300 rounded-full text-sm font-bold border-2 border-black">Community</span>
                  </div>
                </div>

                {/* Categories Section */}
                <div>
                  <h2 className="font-black text-2xl mb-4 text-black">Categories</h2>
                  <div className="flex flex-wrap gap-2">
                    <span className="px-4 py-2 bg-yellow-200 rounded-full text-sm font-bold border-2 border-black">Education</span>
                    <span className="px-4 py-2 bg-yellow-200 rounded-full text-sm font-bold border-2 border-black">Technology</span>
                    <span className="px-4 py-2 bg-yellow-200 rounded-full text-sm font-bold border-2 border-black">Challenge</span>
                  </div>
                </div>
              </div>
            </div>

            {/* Events List */}
            <div className="flex-1">
              {isLoading ? (
                <div className="bg-white p-12 rounded-3xl border-4 border-black text-center">
                  <div className="inline-block w-12 h-12 border-4 border-black border-t-transparent rounded-full animate-spin mb-4"></div>
                  <p className="text-xl font-mono text-black">Loading events...</p>
                </div>
              ) : (
                <div className="space-y-6">
                  {filteredEvents.map((event) => (
                    <div key={event.id} className="bg-gradient-to-br from-blue-200 via-purple-200 to-teal-200 p-8 rounded-3xl border-4 border-black shadow-lg hover:shadow-xl transition-shadow">
                      <div className="flex justify-between items-start mb-4 max-md:flex-col max-md:gap-4">
                        <div>
                          <span className={`inline-block px-4 py-1 rounded-full text-sm font-bold border-2 border-black mb-3 ${
                            event.status === 'upcoming' ? 'bg-blue-300' :
                            event.status === 'present' ? 'bg-green-300' : 'bg-gray-300'
                          }`}>
                            {event.status.charAt(0).toUpperCase() + event.status.slice(1)}
                          </span>
                          <h3 className="font-black text-3xl mb-2 text-black">{event.title}</h3>
                          <p className="font-mono text-lg text-black mb-3">{event.description}</p>
                        </div>
                        <button className="text-white whitespace-nowrap bg-black font-bold text-lg no-underline inline-block px-6 py-2 rounded-3xl border-4 border-solid border-transparent hover:text-black hover:bg-white hover:border-black transition-colors">
                          View Details ‚Üí
                        </button>
                      </div>
                      
                      <div className="flex gap-6 mb-4 flex-wrap font-mono text-base text-black">
                        <div className="flex items-center gap-2">
                          <span className="font-bold">üìÖ</span>
                          <span>{event.date}</span>
                        </div>
                        <div className="flex items-center gap-2">
                          <span className="font-bold">‚è±Ô∏è</span>
                          <span>{event.duration}</span>
                        </div>
                        <div className="flex items-center gap-2">
                          <span className="font-bold">üí∞</span>
                          <span>{event.fee}</span>
                        </div>
                      </div>

                      <div className="flex justify-between items-center flex-wrap gap-4">
                        <div className="flex gap-2">
                          {event.tags.map((tag, idx) => (
                            <span key={idx} className="px-3 py-1 bg-white rounded-full text-sm font-bold border-2 border-black">
                              {tag}
                            </span>
                          ))}
                        </div>
                        <div className="flex items-center gap-2 font-mono text-black">
                          <span className="font-bold">üë•</span>
                          <span>{event.participants} joined</span>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>

          {/* Call to Action */}
          <div className="mt-16 bg-lime-300 p-12 rounded-3xl border-4 border-black">
            <h2 className="font-black text-4xl mb-4 text-black">
              Want to Host an Event?
            </h2>
            <p className="font-mono text-xl mb-6 text-black">
              Share your sustainability ideas with the community. <strong>Create your own event</strong> and inspire others to take action.
            </p>
            <button className="text-white whitespace-nowrap bg-black font-bold text-lg no-underline inline-block px-8 py-3 rounded-3xl border-4 border-solid border-transparent hover:text-black hover:bg-white hover:border-black transition-colors">
              Create Event ‚Üí
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EventsPage;